.tweet{
  position: relative;
  display: flex; flex-direction: column;
  width: 100%; max-width: 320px;
  color: rgba($color: #000, $alpha: 0.8);
  padding: 34px 14px;
  margin-bottom: 34px;
  border-radius: 10px;
  background-color: #fff;
  h4{
    position: relative;
    font-size: 14px;
    padding: 0 14px 0 14px;
    word-break: break-all;
  }
  img{
    position: relative; left: 10px; top: 50%; bottom: 0px;
    margin-top: 18px;
    width: 100px; height: 100px;
    border-radius: 10px;
  }
  span{
    position: absolute; top: -18px; right: 6px;
    font-size: 11px;
    color: rgba($color: #fff, $alpha: 0.8);
  }
  .tweet_actions{
    position: absolute; top: 24px; right: 4px;
    span{
      font-size: 14px;
      color: #04aaff;
      cursor: pointer;
      &:first-of-type{
        margin-right: 18px;

      }
    }
  }
  .tweetEdit{
    .formImg{
      position: absolute; left: 50%; top: 2px; transform: translateX(-50%);
      width: 100px; height: 100px;
      border-radius: 10px;
    }
    .formBtn{
      margin: 15px 0 5px;
      cursor: pointer;
    }
  }
}